__common
-----------------------------------------------*/
.md_main {
  position: relative;
  z-index: 2;
  background: #fff;
  overflow: hidden;
}

/*--------
__top
-----------------------------------------------*/
.top_headNav {
  padding-top: 80px;
  margin-bottom: 100px;
}

.top_headNav ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.top_headNav ul li {
  flex: 0 1 48%;
}

.top_headNav ul li a {
  text-decoration: none;
  color: #008650;
}

.top_headNav ul .top_headNav__thumb img {
  max-width: 100%;
  height: auto;
  display: block;
  border-radius: 20px;
}

.top_headNav__ttl {
  line-height: 1.5;
  padding: .75em 20px;
}

.top_headNav__ttl em {
  font-style: normal;
  display: block;
  font-size: .5em;
  line-height: 1;
}

.md_info__wrap {
  padding: 100px 0 80px 0;
  background: #DEF0E3;
  position: relative;
}

.md_info__wrap:before {
  content: '';
  position: absolute;
  top: 0;
  right: 100%;
  width: 100%;
  height: 100%;
  background: #DEF0E3;
}

.md_info__wrap:after {
  content: '';
  position: absolute;
  top: 0;
  left: 100%;
  width: 10%;
  height: 100%;
  background: #DEF0E3;
}

.md_info__wrap .innr {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.md_info__wrap .innr::after {
  content: '';
  display: block;
  width: 30%;
}

.md_info__ttl {
  position: absolute;
  top: -80px;
  left: 0;
  text-align: center;
  height: 160px;
  width: 160px;
  opacity: 0;
  animation: mv_bar__info 1s ease .8s 1 normal forwards;
}

.md_info__ttl span {
  position: relative;
  z-index: 2;
  color: #008650;
  line-height: 160px;
}

.md_info__ttl:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  background-image: url(../images/gra_bar__brn.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left center;
  height: 160px;
  width: 800px;
  transform: rotate(180deg);
}

@keyframes mv_bar__info {
  0% {
    transform: translateX(-300%);
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

.md_info__article {
  width: 30%;
  padding: 20px 0;
}

.md_info__articleThumb {
  margin-bottom: 1em;
  display: block;
  position: relative;
}

.md_info__articleThumb img {
  max-width: 100%;
  height: auto;
  display: block;
  border-radius: 20px;
}

.md_info__articleTtl a {
  display: block;
  text-decoration: none;
  color: #333;
  line-height: 1.5;
}

.md_info__articleDate {
  position: absolute;
  right: 0px;
  bottom: 15px;
  text-decoration: none;
  color: #333;
  display: inline-block;
  box-sizing: border-box;
  background: #DEF0E3;
  padding: .25em 1em;
  line-height: 1;
  border-radius: 5px 0 0 5px;
}

