__error
-----------------------------------------------*/
.md_pageHeader.page__error {
  padding: 200px 5%;
  text-align: center;
}

@media screen and (max-width: 600px) {
  .md_pageHeader.page__error {
    padding: 120px 5% 60px 5%;
    text-align: left;
  }
}

.md_pageHeader__Title {
