}

@media screen and (max-width: 767px) {
  .md_pageHeader__description br {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .md_pageHeader__description {
    font-size: 12px;
  }
}

.event__img_dummy {
  display: block;
  margin: 0 auto;
}

